.jsdSchemaField{display:flex;flex:1 0 80px;align-items:center;padding:2px;background-color:#eee;background:linear-gradient(90deg, #fafafa, #eee);border-radius:4px;margin:2px;margin-top:0}.jsdSchemaField_title{font-size:13px;margin:0 4px;color:#666;white-space:nowrap}.jsdSchemaField_remove{cursor:pointer;border:0;background-color:transparent;width:24px;height:24px;margin:auto 2px}.jsdSchemaField_remove:hover{background-color:#f88;color:#fff;border-radius:4px}.jsdSchemaField_valueText{font-size:13px}.jsdSchemaField_input{border:1px solid #eee;padding:4px;font-size:14px;box-sizing:border-box;vertical-align:top;border-radius:4px;color:#333;flex-grow:1}.jsdSchemaField_input:hover,.jsdSchemaField_input:focus{border:1px solid #999}